import React from 'react'
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import art from './assets/art.png'
import science from './assets/science.png'
import family from './assets/family.png'
import './Carousel.css'

export const Carousel = () => {

  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1
        }
      }
    ]
  };

  return (
    <div className='overflow-x-hidden mb-8'>
      <Slider {...settings}>
      <div className="">
        <img className='mx-auto' src={science} alt="Illustration sur la thorie des couleurs de Newton" />
        <div>
          <h3>La thorie des couleurs de Newton : comment la couleur blanche est-elle cre ?</h3>
          <p className="text-lg">Explorez la science derrire les couleurs et dcouvrez comment les couleurs sont cres dans le monde rel et sur les crans. Dcouvrez les principes des couleurs additives et soustractives et comment Isaac Newton a expliqu que le blanc est la somme de toutes les couleurs.</p>
        </div>
      </div>
      <div className="px-8">
        <img className='mx-auto' src={art} alt="Illustration famille qui s'amuse avec des couleurs" />
        <div>
          <h3>Jouer avec les couleurs : une exposition ludique pour toute la famille</h3>
          <p className="text-lg">Apprenez aux enfants l'importance de la couleur et comment elles peuvent influencer nos motions et notre perception du monde. Avec des activits ludiques et ducatives, les enfants pourront explorer les couleurs de manire amusante et crative.</p>
        </div>
      </div>
      <div className="px-8">
        <img className='mx-auto' src={family} alt="Illustration pour prsenter la couleur dans l'art" />
        <div>
          <h3>L'art de la couleur : explorez l'utilisation des couleurs dans l'art</h3>
          <p className="text-lg">Dcouvrez comment les artistes ont utilis la couleur pour exprimer des motions et des ides  travers l'histoire de l'art. Admirez les uvres d'art colores de clbres artistes modernes et contemporains.</p>
        </div>
      </div>
    </Slider>
    </div>
  );
};

export default Carousel;